import React from "react";

import { HeadFC, PageProps } from "gatsby";
import { graphql } from "gatsby";

import { IProjectsPageContext, IProjectsPageData } from "../types/project.type";

import Banner from "../components/common/banner";
import ContentContainer from "../components/common/contentContainer";
import ProjectPreview from "../components/projects/projectPreview";
import Pagination from "../components/common/pagination";

const Projects = ({
  data,
  pageContext,
}: PageProps<IProjectsPageData, IProjectsPageContext>) => {
  const projects = data.allMarkdownRemark.nodes;
  const { currentPage, pagesQuantity } = pageContext;

  return (
    <>
      <Banner
        heightClass="h-136"
        title="CodeCave projects"
        text="Our super amazing projects"
        borderBottom
      />
      <ContentContainer className="flex-col items-center p-12">
        <div className="flex flex-wrap w-full justify-around py-14">
          {projects
            ? projects.map((project) => (
                <ProjectPreview
                  key={project.id}
                  project={project}
                  theme="dark"
                  className="basis-full md:basis-3/4 lg:basis-2/5 m-5"
                />
              ))
            : null}
        </div>
        {pagesQuantity > 1 ? (
          <Pagination
            section={"projects"}
            pagesQuantity={pagesQuantity}
            currentPage={currentPage}
          />
        ) : null}
      </ContentContainer>
    </>
  );
};

export default Projects;

export const Head: HeadFC = () => <title>CodeCave Projects</title>;

export const query = graphql`
  query ($skip: Int!, $limit: Int!) {
    allMarkdownRemark(
      filter: { fields: { sourceName: { eq: "projects" } } }
      sort: { frontmatter: { date: DESC } }
      limit: $limit
      skip: $skip
    ) {
      nodes {
        id
        frontmatter {
          date(formatString: "MMM DD, YYYY")
          title
          text
          stack
          cover {
            childImageSharp {
              gatsbyImageData(
                formats: [AUTO, WEBP, AVIF]
                placeholder: BLURRED
                width: 600
              )
            }
          }
        }
      }
    }
  }
`;
